import React, { Fragment } from "react";
import _get from "lodash/get";
import myapi from "../../api";
import { FormControl, Button, Select, Message } from "tinper-bee";
import "bee-form-control/build/FormControl.css";
import "bee-button/build/Button.css";
import "bee-select/build/Select.css";
import "bee-message/build/Message.css";
import { withRouter } from "react-router-dom";
import Header from "../common/Header";
import Content from "../common/Content";
import AuthImg from "./auth.png";
import BasicImg from "./basic.png";
import LinkManImg from "./linkman.png";
import "./index.css";
const Option = Select.Option;
let accountInfo = [
  {
    title: "",
    value: "basInfo.bindBankCard",
  },
  {
    title: "",
    value: "basInfo.bindCardAccName",
  },
  {
    title: "",
    value: "basInfo.bindBankName",
  },
  {
    title: "",
    value: "basInfo.bindBankNo",
  },
  {
    title: "",
    value: "basInfo.accNo",
  },
];

class IsvDetail extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      allValue: {
        basInfo: {
          memberName: "", //
          regAddress: "", //
          unitLice: "", //
          custCertSdate: "", //
          custCertEdate: "", //
          custCertPath: "", //
          regDate: "", //
          businessType: "", //
          finaPhone: "", //
          legalName: "", //
          corporateCertTypeDesc: "", //
          corporateCertNo: "", //
          corporateCertSdate: "", //
          corporateCertEdate: "", //
          operatorMobile: "", //
          operatorName: null, //
          operatorCertTypeDesc: "", //
          operatorCertNo: null, //
          operatorCertSdate: null, //
          operatorCertEdate: null, //
          bindBankCard: "", //
          bindCardAccName: "", //
          bindBankNo: "", //
          bindBankName: "", //
        },
        data: {
          userId: "",
          statue: "",
          isvName: "", //
          legalPerson: "", //
          industry: "", //
          location: "", //
          licenceNumber: "", //
          licencePic: "", //
          chanpayAccount: "", //
          partnerType: "", // 1 2 3 4 5- 6-()
        },
      }, //
      basicInfo: [],
      personInfo: [],
      isNew: null,
      partnerType: false,
      partnerTypeValue: "",
      partnerBackup: "1",
      chanpayAccount: false,
      chanpayAccountValue: "",
      chanpayBackup: "",
    };
  }

  componentDidMount() {
    this.getData();
  }

  getData = () => {
    var vals = this.props.match.params;
    var id = vals.id;
    this.setState({
      isvId: id,
    });
    fetch(myapi.BASE_URL + `/market/isv/info?isv_id=${id}`, {
      method: "GET",
      mode: "cors",
      headers: {
        "Content-Type": "application/json;charset=UTF-8",
        "Access-Control-Allow-Credentials": "true",
        "Access-Control-Allow-Headers": "X-Requested-With",
        "Access-Control-Allow-Methods": "GET,POST,OPTIONS",
        "Access-Control-Allow-Origin": "*",
      },
      credentials: "include",
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.status === 1) {
          var allCarouse = {
            basInfo: response.basInfo,
            data: response.data,
          };
          console.log(allCarouse);
          let basicInfo = [
            {
              title: "",
              value: response.basInfo ? "basInfo.memberName" : "data.isvName",
            },
            {
              title: "",
              value: response.basInfo
                ? "basInfo.unitLice"
                : "data.licenceNumber",
            },
            {
              title: "",
              value: response.basInfo
                ? "basInfo.businessType"
                : "data.industry",
            },
            {
              title: "",
              value: response.basInfo ? "basInfo.regAddress" : "data.location",
            },
            {
              title: "",
              value: "basInfo.regDate",
            },
            {
              title: "",
              value: "basInfo.custCertSdate",
            },
            {
              title: "",
              value: "basInfo.custCertEdate",
            },
            {
              title: "",
              value: "basInfo.finaPhone",
            },
          ];
          let personInfo = [
            {
              title: "",
              value: response.basInfo
                ? "basInfo.legalName"
                : "data.legalPerson",
            },
            {
              title: "",
              value: "basInfo.corporateCertTypeDesc",
            },
            {
              title: "",
              value: "basInfo.corporateCertNo",
            },
            {
              title: "",
              value: "basInfo.corporateCertSdate",
            },
            {
              title: "",
              value: "basInfo.corporateCertEdate",
            },
            {
              title: "",
              value: "basInfo.operatorName",
            },
            {
              title: "",
              value: "basInfo.operatorCertTypeDesc",
            },
            {
              title: "",
              value: "basInfo.operatorCertNo",
            },
            {
              title: "",
              value: "basInfo.operatorCertSdate",
            },
            {
              title: "",
              value: "basInfo.operatorCertEdate",
            },
            {
              title: "",
              value: "basInfo.operatorMobile",
            },
          ];
          this.setState({
            allValue: allCarouse,
            basicInfo,
            personInfo,
            isNew: response.basInfo ? false : true,
            partnerBackup: response.data.partnerType,
            chanpayBackup: response.data.chanpayAccount,
          });
        } else {
          Message.destroy();
          Message.create({ content: response.msg, color: "danger" });
        }
      });
  };

  renderType = (type) => {
    let value = null;
    switch (type) {
      case "1":
        value = "";
        break;
      case "2":
        value = "";
        break;
      case "3":
        value = "";
        break;
      case "4":
        value = "";
        break;
      case "5":
        value = "";
        break;
      case "6":
        value = "()";
        break;
    }
    return value;
  };

  show = (type) => {
    this.setState({ [type]: true });
  };

  hide = (type) => {
    this.setState({ [type]: false });
  };

  chanpayChange = (v) => {
    this.setState({ chanpayBackup: v });
  };

  partnerChange = (v) => {
    this.setState({ partnerBackup: v });
  };

  save = (type) => {
    let { partnerBackup, chanpayBackup, allValue } = this.state;
    let { userId } = allValue.data;
    let query = {
      userId,
      chanpayAccount: chanpayBackup,
      partnerType: partnerBackup,
    };
    fetch(myapi.BASE_URL + `/market/isv/operator/update`, {
      method: "POST",
      mode: "cors",
      body: JSON.stringify(query),
      headers: {
        "Content-Type": "application/json;charset=UTF-8",
        "Access-Control-Allow-Credentials": "true",
        "Access-Control-Allow-Headers": "X-Requested-With",
        "Access-Control-Allow-Methods": "GET,POST,OPTIONS",
        "Access-Control-Allow-Origin": "*",
      },
      credentials: "include",
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.status === 1) {
          this.getData();
          this.setState({ [type]: false });
        } else {
          Message.destroy();
          Message.create({ content: response.msg, color: "danger" });
        }
      });
  };

  enable = () => {
    let { allValue } = this.state;
    let { userId, status } = allValue.data;
    let query = {
      userId,
      status: status === "1" ? "0" : "1",
    };
    fetch(myapi.BASE_URL + `/market/isv/operator/update`, {
      method: "POST",
      mode: "cors",
      body: JSON.stringify(query),
      headers: {
        "Content-Type": "application/json;charset=UTF-8",
        "Access-Control-Allow-Credentials": "true",
        "Access-Control-Allow-Headers": "X-Requested-With",
        "Access-Control-Allow-Methods": "GET,POST,OPTIONS",
        "Access-Control-Allow-Origin": "*",
      },
      credentials: "include",
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.status === 1) {
          this.getData();
        } else {
          Message.destroy();
          Message.create({ content: response.msg, color: "danger" });
        }
      });
  };

  render() {
    let {
      allValue,
      basicInfo,
      personInfo,
      isNew,
      partnerType,
      partnerBackup,
      chanpayAccount,
      chanpayBackup,
    } = this.state;
    return (
      <Fragment>
        <Header
          style={{ background: "#fff", padding: 0 }}
          title=""
          back
        />
        <Content style={{ width: "100%" }}>
          <div className="isv-detail">
            <div className="isv-detail-zone">
              <div className="isv-detail-zone-row">
                <img className="isv-detail-zone-icon" src={BasicImg} />
                <div className="isv-detail-zone-header"></div>
                <Button
                  className="isv-detail-zone-btn"
                  style={{ marginRight: 0 }}
                  colors="dark"
                  onClick={this.enable}
                >
                  {allValue.data.status === "1" ? "" : ""}
                </Button>
              </div>
              {basicInfo.length > 0
                ? basicInfo.map((item) => {
                    return (
                      <div className="isv-detail-zone-item">
                        <div className="isv-detail-zone-item-title">
                          {item.title}
                        </div>
                        <div className="isv-detail-zone-item-value">
                          {_get(allValue, item.value)}
                        </div>
                      </div>
                    );
                  })
                : null}
              <div className="isv-detail-zone-item">
                <div className="isv-detail-zone-item-title"></div>
                {partnerType ? (
                  <Select
                    className="isv-detail-zone-item-select"
                    value={partnerBackup}
                    onChange={this.partnerChange}
                  >
                    <Option value="1"></Option>
                    <Option value="2"></Option>
                    <Option value="3"></Option>
                    <Option value="4"></Option>
                    <Option value="5"></Option>
                    <Option value="6">()</Option>
                  </Select>
                ) : (
                  <div className="isv-detail-zone-item-value">
                    {this.renderType(allValue.data.partnerType)}
                  </div>
                )}

                {partnerType ? (
                  <Fragment>
                    <Button
                      className="isv-detail-zone-item-edit"
                      colors="dark"
                      onClick={this.save.bind(null, "partnerType")}
                    >
                      
                    </Button>
                    <Button
                      className="isv-detail-zone-item-edit"
                      colors="secondary"
                      onClick={this.hide.bind(null, "partnerType")}
                    >
                      
                    </Button>
                  </Fragment>
                ) : (
                  <Button
                    className="isv-detail-zone-item-btn"
                    bordered
                    onClick={this.show.bind(null, "partnerType")}
                  >
                    
                  </Button>
                )}
              </div>
              <div className="isv-detail-zone-item">
                <div className="isv-detail-zone-item-title">
                  
                </div>
                <img
                  className="isv-detail-zone-item-value"
                  src={
                    isNew ? allValue.data.licencePic : allValue.basInfo.custCertPath
                  }
                />
              </div>
            </div>

            <div className="isv-detail-zone">
              <div className="isv-detail-zone-row">
                <img className="isv-detail-zone-icon" src={LinkManImg} />
                <div className="isv-detail-zone-header">
                  
                </div>
              </div>
              {personInfo.length > 0
                ? personInfo.map((item) => {
                    return (
                      <div className="isv-detail-zone-item">
                        <div className="isv-detail-zone-item-title">
                          {item.title}
                        </div>
                        <div className="isv-detail-zone-item-value">
                          {_get(allValue, item.value)}
                        </div>
                      </div>
                    );
                  })
                : null}
            </div>

            <div className="isv-detail-zone">
              <div className="isv-detail-zone-row">
                <img className="isv-detail-zone-icon" src={AuthImg} />
                <div className="isv-detail-zone-header"></div>
              </div>
              {accountInfo.map((item) => {
                return (
                  <div className="isv-detail-zone-item">
                    <div className="isv-detail-zone-item-title">
                      {item.title}
                    </div>
                    <div className="isv-detail-zone-item-value">
                      {_get(allValue, item.value)}
                    </div>
                  </div>
                );
              })}
              <div className="isv-detail-zone-item">
                <div className="isv-detail-zone-item-title"></div>
                {chanpayAccount ? (
                  <FormControl
                    className="isv-detail-zone-item-input"
                    value={chanpayBackup}
                    onChange={this.chanpayChange}
                  />
                ) : (
                  <div className="isv-detail-zone-item-value">
                    {allValue.data.chanpayAccount}
                  </div>
                )}
                {chanpayAccount ? (
                  <Fragment>
                    <Button
                      className="isv-detail-zone-item-edit"
                      colors="dark"
                      onClick={this.save.bind(null, "chanpayAccount")}
                    >
                      
                    </Button>
                    <Button
                      className="isv-detail-zone-item-edit"
                      colors="secondary"
                      onClick={this.hide.bind(null, "chanpayAccount")}
                    >
                      
                    </Button>
                  </Fragment>
                ) : (
                  <Button
                    className="isv-detail-zone-item-btn"
                    bordered
                    onClick={this.show.bind(null, "chanpayAccount")}
                  >
                    
                  </Button>
                )}
              </div>
            </div>
          </div>
        </Content>
      </Fragment>
    );
  }
}
export default withRouter(IsvDetail);
